import {Component} from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import {BatteriesService} from '../batteries.service'
import {FormControl} from '@angular/forms';
import {MachinesService} from '../machines.service'
import { BatteryCardsComponent } from '../battery-cards/battery-cards.component';
import { DomSanitizer } from '@angular/platform-browser';
import {  FileUploader } from 'ng2-file-upload';
import {MatDialog, MatDialogConfig} from "@angular/material";

export interface Battery{
  name: string;
  batteryDescription:string;
  voltage:number;
  capacity: number;
  energy: number;
  width:number;
  len:number;
  height:number;
  weight:number;
  cellWeight:number;
  image:false;
  pdf:false; 
  
}
// const ELEMENT_DATA: Battery[] =[];
const uploadAPI = '/api/BatteryUpload';
const uploadPDFAPI = '/api/BatteryPDFUpload';
declare var require: any
const FileSaver = require('file-saver');




/**
 * @title Table with filtering
 */
@Component({
  selector: 'tabel',
  styleUrls: ['tabel.component.css'],
  templateUrl: 'tabel.component.html',
})
export class TabelComponent {
  disableSelect = true;
  Machines = new FormControl();
  Models = new FormControl();
  data: any = null;
  dataSource =[];
  ShowAdd = false;
  Update = false;
  UpdateData: Battery[]=[];
  Catergory =[]
  CatModels =[]
  localImageUrl='';
  PDFurl = '';
  SomethingToUpload = false;
  PDFtoUpload  = false;

  DeleteImage = false; 
  UpdatePDF = false;


  isThereImage = false;
 
  AddBatteryData: Battery[] =[];
  UpdateImageUrl = ''
  UpdateImage = false;
  

  public uploader: FileUploader = new FileUploader({ url: uploadAPI, itemAlias: 'file' });
  public PDFuploader: FileUploader = new FileUploader({allowedFileType:['pdf'], url: uploadPDFAPI, itemAlias: 'file' });

  constructor(public http: Http, public BS: BatteriesService, public MS: MachinesService, public sanitizer:DomSanitizer) {
  this.getTableData()    
  this.getMachines()

  
  this.uploader.onAfterAddingFile = (file) => {
    this.SomethingToUpload = true
    this.isThereImage = true
    this.AddBatteryData['image'] = true
    this.UpdateData['image'] = true
    this.UpdateImage = false
    // this.UpdateData['image'] = true
    file.withCredentials = false;
    let url = (window.URL) ? window.URL.createObjectURL(file._file) : (window as any).webkitURL.createObjectURL(file._file);
    this.localImageUrl = url;

    console.log(this.localImageUrl)


   };


   this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
    console.log('FileUpload:uploaded successfully:', item, status, response);
    this.SomethingToUpload = false; 
    this.localImageUrl='';

    alert('Your file has been uploaded successfully');
};



this.PDFuploader.onAfterAddingFile = (file) => {
  this.PDFtoUpload = true; 
  file.withCredentials = false;
  this.AddBatteryData['pdf'] = true
  this.UpdateData['pdf'] = true

  console.log('PDF file waiting')
 };

 this.PDFuploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
  console.log('FileUpload:uploaded successfully:', item, status, response);
  this.PDFtoUpload = false; 

  alert('Your PDF has been uploaded successfully');
  console.log('PDF UPLOADED')
};
    
   }


   downloadPdf(pdfUrl: string, pdfName: string ) {

    FileSaver.saveAs(pdfUrl, pdfName);
  }
 

  displayedColumns: string[] = ['name', 'description', 'voltage', 'capacity','energy','width','length','height','weight','UpdateButtons','DelButtons'];
  
  addBattery()
  {
    //console.log(this.AddBatteryData);
    this.BS.addBattery(this.AddBatteryData).subscribe(data => 
      {
        //console.log(data);
        this.getTableData();
        for (var x in this.AddBatteryData)
        {
          //console.log( this.AddBatteryData[x],x)
          this.AddBatteryData[x] =null;
          
        }
        var res = JSON.parse(data['_body'])
        if ( res['message'] =='Successful'){
          if(this.isThereImage){
           this.uploader.uploadAll();
           this.localImageUrl='';
          }

          if(this.PDFtoUpload)
          {
            this.PDFuploader.uploadAll();
            console.log('Uploading PDF... ')
          }
 
        this.ShowAdd =!this.ShowAdd;
    
    
        }
    });
      

  }
  DisplayUpdateData(Bat){
    this.Update = !this.Update;

    this.UpdateData = Bat;
    
    if (Bat.image){
      // this.UpdateImage = Bat.image;
      this.UpdateImageUrl = "/batteries/"+Bat.name+'/'+Bat.name+'.png'+"?" + new Date().getTime();
    }

    if(Bat.pdf)
    {

      this.PDFurl = "/batteries/"+Bat.name+'/'+Bat.name+'.pdf'
    }
  }

  deleteImage(name)
  {
    this.DeleteImage = true; 
    this.UpdateImageUrl = '' 
    this.UpdateImage = true;
    this.UpdateData['image'] = false; 

    this.BS.deleteBatteryImage(name).subscribe(data=>{
  
      console.log(data)


      this.UpdateBatData(this.UpdateData)
    });
  }

  deletePDF(name)
  {


    this.UpdateData['pdf'] = false; 

    this.BS.deleteBatteryPDF(name).subscribe(data=>{
  
      console.log(data)


      this.UpdateBatData(this.UpdateData)
    });
  }







  deleteData(i)
  {
    //console.log(i);
    var batId = this.dataSource[i]['_id']
    //console.log(this.dataSource[i]['_id'])
    this.BS.DelBat(batId).subscribe(data => 
      {
        //console.log(data);
        this.getTableData();  
    
    
    
    });

  }

  UpdateBatData(updateData)
  {
    //console.log(updateData);

    this.BS.UpdateBat(updateData).subscribe(data=>{
  
      console.log(data)

      for (var x in this.UpdateBatData)
      {
        //console.log( this.UpdateBatData[x],x)
        this.UpdateBatData[x] =null;
       
      }

      this.Update = !this.Update;

      var res = JSON.parse(data['_body'])
      console.log(res['message']);

      if ( res['message'] =='Battery Info Updated'){
        
      if(this.SomethingToUpload){
        console.log('Uploading Image')
        this.uploader.uploadAll();
      }
      if(this.PDFtoUpload)
      {
        this.PDFuploader.uploadAll();
      }
      

       
        
      }



      this.getTableData()
    });

  }



  

  getTableData()
  {
    this.BS.getBatteries() .subscribe((data) =>{ 
      var batteryData = JSON.parse(data['_body'])
      this.dataSource = batteryData['batteries']
      console.log(this.dataSource)
        } );

  }
 

  getMachines()
  { var a = []
    this.MS.getMachines().subscribe((data)=>{
      var machineData=JSON.parse(data['_body'])
      var machines = machineData['machines']
      for (var ma in machines){
      if(this.Catergory.indexOf(machines[ma].catergory)==-1){ 
        this.Catergory.push(machines[ma].catergory)
      }
      }
      // //console.log(this.Catergory)
      for (var cat in this.Catergory){
        // //console.log(cat)
        var temp = []
        for (var ma in machines){
          if (this.Catergory[cat]==machines[ma].catergory)
          {
            temp.push(machines[ma].model)
          }
          // //console.log(temp)
          
        }



        var test = {
          cat: this.Catergory[cat],
          models: temp
        }

        this.CatModels.push(test)
      }
      // //console.log(this.CatModels)


    })

  }

}
