import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';



@Injectable({
  providedIn: 'root'
})

// DeleteBatteryImage
export class BatteriesService {
  url = '/api/battery';
  constructor(private http:Http) {
    
   }
  

  getBatteries(){
    return this.http.get(this.url)
  }
  deleteBatteryImage(name)
  {
    var URL = '/api/DeleteBatteryImage'
    console.log(name)
    return this.http.post(URL, {name}); 
  }

  deleteBatteryPDF(name)
  {
    var URL = '/api/DeleteBatteryPDF'
    console.log(name)
    return this.http.post(URL, {name}); 
  }
  addBattery(bat)
  {
    var cat = []
    var models=[]
    for (var num in bat.models)
    {
      if(!(bat.models[num].cat in cat))
      {
        cat.push(bat.models[num].cat)
      }
      for (var mo in bat.models[num].models){
        models.push(bat.models[num].models[mo]) 
      }
      
    }
    //console.log('Cats',cat)
    //console.log('Models',models)

    var battery = {
      name: bat.name,
      batteryDescription:bat.voltage+'V '+bat.capacity+'Ah '+bat.energy+'kWh',
      voltage:bat.voltage,
      capacity: bat.capacity,
      energy: bat.energy,
      width: bat.width,
      len: bat.len,
      height: bat.height,
      weight: bat.weight,
      cellWeight: bat.cellWeight,
      categories: cat,
      models: models,
      image:bat.image,
      pdf: bat.pdf
    }
    //console.log(bat.voltage)
    //console.log('Adding Battery');
    //console.log('Battery Added');
    return this.http.post(this.url, {battery});
    

  }

  DelBat (id){
    var deleteUrl = this.url+'/'+id;
    return this.http.delete(deleteUrl);
    

  }

  UpdateBat(bat)
  {
    var id = bat['_id'];
    //console.log(bat)
    var cat = []
    var models=[]
    for (var num in bat.models)
    {
      if(!(bat.models[num].cat in cat))
      {
        cat.push(bat.models[num].cat)
      }
      for (var mo in bat.models[num].models){
        models.push(bat.models[num].models[mo]) 
      }
      
    }
    //console.log('Cats',cat)
    //console.log('Models',models)

    var battery = {
      name: bat.name,
      batteryDescription:bat.voltage+'V '+bat.capacity+'Ah '+bat.energy+'kWh',
      voltage:bat.voltage,
      capacity: bat.capacity,
      energy: bat.energy,
      width: bat.width,
      len: bat.len,
      height: bat.height,
      weight: bat.weight,
      cellWeight: bat.cellWeight,
      image:bat.image,
      pdf:bat.pdf


    }
    var updateUrl = this.url+'/'+id;
    return this.http.put(updateUrl,{battery});

  }


}
