import { DomSanitizer } from '@angular/platform-browser';
import {Component,ViewChild,OnInit} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';

import {MatTableDataSource} from '@angular/material/table';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import {BatteriesService} from '../batteries.service'
import {MachinesService} from '../machines.service'
import {FormControl} from '@angular/forms';
import { BatteryCardsComponent } from '../battery-cards/battery-cards.component';
import {  FileUploader } from 'ng2-file-upload';

const uploadAPI = '/api/MachineUpload';
// const UpdateUploadAPI = '/api/UpdateImageUpload';

const uploadPDFAPI = '/api/MachinePDFUpload';
declare var require: any
const FileSaver = require('file-saver');

export interface Machine{
  OEM:String,
  catergory: String,
  model:String,
  voltage:number,
  minCap:number,
  maxCap:number, 
  energy:number,
  minWeight:number,
  width:number,
  len:number,
  height:number,
  verified:boolean,
  image:boolean,
  pdf:boolean

  
  
}


@Component({
  selector: 'app-machines',
  templateUrl: './machines.component.html',
  styleUrls: ['./machines.component.css']
})
export class MachinesComponent implements OnInit {


  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;dataSource: MatTableDataSource<Element>;
  
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  
  public imagePath;
  imgURL: any;
  public message: string;
  Batteries = new FormControl();
  BatteryNames=[];
  data: any = null;
  localImageUrl ='';

  localUpdateImageUrl ='';
  selectedImage: File = null;
  isThereImage = false;
  image: FormData;
  UpdateImageUrl ='';
  ShowAdd = false;
  Update = false;

  UpdateImage = false;
  UpdateData: Machine[]=[];
  UpdatePDF = false ;
  
 
  AddMachineData: Machine[] =[];



  PDFtoUpload = false;

  public uploader: FileUploader = new FileUploader({ url: uploadAPI, itemAlias: 'file' });

  // public UpdateUploader: FileUploader = new FileUploader({ url: UpdateUploadAPI, itemAlias: 'UpdateFile' });

  public PDFuploader: FileUploader = new FileUploader({allowedFileType:['pdf'], url: uploadPDFAPI, itemAlias: 'file' });




  constructor(public http: Http, public BS: BatteriesService, public MS: MachinesService,public sanitizer:DomSanitizer) {
  this.getTableData()
  this.getBats()


    
   }
     ngOnInit() {
    this.getTableData()

    this.uploader.onAfterAddingFile = (file) => {
      this.isThereImage = true
      this.AddMachineData['image'] = true
      this.UpdateData['image'] = true
      file.withCredentials = false;
      let url = (window.URL) ? window.URL.createObjectURL(file._file) : (window as any).webkitURL.createObjectURL(file._file);
      this.localImageUrl = url;

      console.log(this.localImageUrl)

      this.UpdateImage = false;
     };
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
         console.log('FileUpload:uploaded successfully:', item, status, response);
        
         this.localImageUrl='';

         alert('Your file has been uploaded successfully');
    };


    
    this.PDFuploader.onAfterAddingFile = (file) => {
      this.PDFtoUpload = true; 
      file.withCredentials = false;
      this.AddMachineData['pdf'] = true
      this.UpdateData['pdf'] = true
    
      console.log('PDF file waiting')
     };
    
     this.PDFuploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
      console.log('FileUpload:uploaded successfully:', item, status, response);
      this.PDFtoUpload = false; 
    
      alert('Your PDF has been uploaded successfully');
      console.log('PDF UPLOADED')
    };





  }

   applyFilter(event: Event) {
    var test = (event.target as HTMLInputElement).value
    const filterValue = test.toString();

    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  downloadPdf(oem,cat,model ) {
    var pdfUrl = '/machines/'+oem+'/'+cat+'/'+model+'/'+oem+' '+cat+' '+model+'.pdf'
    var pdfName = oem+' '+cat+' '+model
    FileSaver.saveAs(pdfUrl, pdfName);
  }
  DeleteImage(){

    
    this.UpdateData['image'] = false; 

    this.UpdateData['DelImage'] = true
    this.Update = false
    console.log(this.UpdateData)
    this.localImageUrl=''
    var url = this.UpdateData['OEM']+'/'+this.UpdateData['catergory']+'/'+this.UpdateData['model']+'/'+this.UpdateData['OEM']+' '+this.UpdateData['catergory']+' '+this.UpdateData['model']+'.png';
    
    
    console.log(url)
    this.MS.deleteImage(url).subscribe(data=>{
        console.log(data)
        console.log(this.UpdateData)
        this.MS.UpdateMachine(this.UpdateData).subscribe(data =>{console.log(data)});

    })
  }
 
  preview(files) {
    if (files.length === 0)
      return;
 
    var mimeType = files[0].type;
    
    if (mimeType.match(/image\/*/) == null) {
      this.message = "Only images are supported.";
      return;
    }
    
    this.selectedImage= <File> files[0];
   



    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]); 
    reader.onload = (_event) => { 
      this.imgURL = reader.result;
      
    }
  }
 


  displayedColumns: string[] = ['OEM', 'Catergory', 'Model', 'voltage','minCap','maxCap','Energy','minWeight','Width','Length','Height','verified','UpdateButtons', 'DelButtons'];
  


onFileSelected(event){
  this.selectedImage = event.target.files[0];

 
  console.log(this.AddMachineData);
  console.log(this.selectedImage)
}




  addBattery()
  {
    //console.log(this.AddMachineData);
 
    this.MS.addMachine(this.AddMachineData).subscribe(data => 
      {
        var res = JSON.parse(data['_body'])
        console.log(res['message']);

        if ( res['message'] =='Successful'){
         if(this.isThereImage){
          this.uploader.uploadAll();
          this.localImageUrl='';
         }

         if(this.PDFtoUpload)
         {
           this.PDFuploader.uploadAll();
           console.log('Uploading PDF... ')
         }
          
        }
        this.getTableData();
        for (var x in this.AddMachineData)
        {
          //console.log( this.AddMachineData[x],x)
          this.AddMachineData[x] =null;
          
        }
 
        this.ShowAdd =!this.ShowAdd;
    
    
    
    });
      

  }
  DisplayUpdateData(element){
    this.Update = !this.Update;
    //console.log(element)
    this.UpdateData = element;
    if(this.UpdateData['image'])
    {
      this.UpdateImageUrl = this.UpdateData['OEM']+'/'+this.UpdateData['catergory']+'/'+this.UpdateData['model']+'/'+this.UpdateData['OEM']+' '+this.UpdateData['catergory']+' '+this.UpdateData['model']+'.png'+"?" + new Date().getTime();
    }


  }

  deletePDF(oem,cat,model)
  {

    var name  = oem+'/'+cat+'/'+model+'/'+oem+' '+cat+' '+model
    this.UpdateData['pdf'] = false; 

    this.MS.deleteBatteryPDF(name).subscribe(data=>{
  
      console.log(data)


      this.UpdateMachineData(this.UpdateData)
    });
  }



  UpdateImageURL()
  {
    this.UpdateImageUrl = this.UpdateImageUrl+"?" + new Date().getTime();
  }
  deleteData(i)
  {
    console.log(i);
    console.log(this.dataSource)
    console.log(this.dataSource['filteredData'][i]['_id'])
    var DelPicName = ''
    var machineId = this.dataSource['filteredData'][i]['_id']


    if (this.dataSource['filteredData'][i]['image']){
    
    DelPicName = this.dataSource['filteredData'][i]['OEM']+" "+this.dataSource['filteredData'][i]['catergory']+' '+this.dataSource['filteredData'][i]['model'];
    console.log('This is the pic name: '+DelPicName)
    }
    else{
      DelPicName = 'There is no image' 
      console.log('There is no image')

    }
    // //console.log(this.dataSource[i]['_id'])
   
   
    this.MS.DelMachine(machineId).subscribe(data => 
      {
        // //console.log(data);
        this.getTableData();  
    
    
    
    });
    

  }


  UpdateMachineData(updateData)
  {
    //console.log(updateData);

    this.MS.UpdateMachine(updateData).subscribe(data=>{
  
      //console.log(data)

      for (var x in this.UpdateMachineData)
      {
        //console.log( this.UpdateMachineData[x],x)
        this.UpdateMachineData[x] =null;
       
      }

      this.Update = !this.Update;


      var res = JSON.parse(data['_body'])
      console.log(res['message']);

      if ( res['message'] =='Machine Info Updated'){

      if(this.isThereImage){
      this.uploader.uploadAll();
      }
      if(this.PDFtoUpload)
      {
        this.PDFuploader.uploadAll();
        console.log('Uploading PDF... ')
      }
       
        
      }



      
    });

  }




  getTableData()
  {
    this.MS.getMachines() .subscribe((data) =>{ 
      var machineData = JSON.parse(data['_body'])
      this.dataSource =  new MatTableDataSource(machineData['machines'])
      this.dataSource.paginator = this.paginator;

      
        } );

      }

  getBats()
  {
    this.BS.getBatteries().subscribe((data) =>{ 
      var batteries = JSON.parse(data['_body'])
      var bats = batteries['batteries']
      for (var bat in bats){
        this.BatteryNames.push(bats[bat].name)
        // //console.log(bats[bat].name)

      }
      // //console.log(bats);
        } );
  }
}
